import { GetComponentProps, styled, useOptions } from 'topwrite';
import { ReactComponent as Icon } from '../../images/weapp.svg';

declare module 'topwrite' {
    export interface OptionsStateType {
        weapp: string | false;
    }
}

export default function Menu({ Component }: GetComponentProps<'page:menu:item'>) {

    const { weapp } = useOptions();

    if (!weapp) {
        return null;
    }

    const tooltip = <ToolTip>
        <img src={weapp} />
        <p>扫一扫阅读</p>
    </ToolTip>;

    return <Component tooltip={{
        overlay: tooltip,
        placement: 'leftBottom'
    }}>
        <Icon />
    </Component>;
}

const ToolTip = styled.div`
  text-align: center;

  img {
    padding: 8px;
    background: #FFFFFF;
    width: 150px;
    height: 150px;
  }

  p {
    margin-bottom: 0;
  }
`;
